import { Route, Routes } from "react-router-dom";
import BlogList from "./BlogList";
import User from "./User";
import Blog from "./Blog";
import { useContext, useRef, useState } from "react";
import { useMutation, useQuery, useQueryClient } from "react-query";
import blogs from "../services/blogs";
import users from "../services/users";
import NotificationContext from "../NotificationContext";
import Users from "./Users";
import { Paper } from "@mui/material";
import Navbar from "./Navbar";

const InnerApp = () => {
  const { notification, createdNotif, invalidNotif, missingNotif } =
    useContext(NotificationContext);

  const queryClient = useQueryClient();
  const { data,isLoading } = useQuery("blogs", blogs.getAll);
  const { data: userData, status: userStatus } = useQuery(
    "users",
    users.getAll
  );
  const [newBlog, setNewBlog] = useState({
    category: "",
    location: "",
    pop: 0,
  });
  const blogFormRef = useRef();
  const newBlogMutation = useMutation(blogs.create, {
    onSuccess: () => {
      queryClient.invalidateQueries("blogs");
    },
    onError: () => missingNotif(),
  });
  const createBlog = async (e) => {
    e.preventDefault();
    const newObject = {
      motive: newBlog.motive,
      category: newBlog.category,
      location: newBlog.location,
      pop: newBlog.pop,
    };
    createdNotif(newObject);
    await newBlogMutation.mutateAsync(newObject);
    setNewBlog({ motive: "", category: "", location: "", pop: 0 });
    blogFormRef.current.toggleVisibility();
  };

  return (
    <Paper>
      <Navbar />
      <Routes>
        <Route
          path="/"
          element={
            <BlogList
              blogFormRef={blogFormRef}
              newBlog={newBlog}
              setNewBlog={setNewBlog}
              createBlog={createBlog}
              blogList={data}
            />
          }
        />
        <Route
          path="/users"
          element={<Users userData={userData} userStatus={userStatus} />}
        ></Route>
        <Route
          path="/users/:id"
          element={<User userData={userData} userStatus={userStatus} />}
        ></Route>
        <Route path="/blogs/:id" element={<Blog blogList={{data,isLoading}} />} />
      </Routes>
    </Paper>
  );
};

export default InnerApp;
