import { Button, TextField } from "@mui/material";

const BlogForm = ({ createBlog, setNewBlog, newBlog }) => {
  const handleChange = (e) => {
    const { name, value } = e.target;
    setNewBlog((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <div>
      <form onSubmit={createBlog}>
        <div>
          <TextField
            type="text"
            value={newBlog.motive}
            id="motive"
            name="motive"
            onChange={handleChange}
            label="Motive"
            variant="filled"
            sx={{mb:2}}
          />
        </div>
        <div>
          <TextField
            type="text"
            value={newBlog.category}
            id="category"
            name="category"
            onChange={handleChange}
            label="Category"
            variant="filled"
            sx={{mb:2}}
          />
        </div>
        <div>
          <TextField
            type="text"
            value={newBlog.location}
            id="location"
            name="location"
            onChange={handleChange}
            label="Location"
            variant="filled"
            sx={{mb:2}}
          />
        </div>
        <div>
          <TextField
            type="number"
            value={newBlog.pop}
            id="pop"
            name="pop"
            onChange={handleChange}
            label="How many people?"
            variant="filled"
          />
        </div>
        <Button component ={Button} type="submit">Save</Button>
      </form>
    </div>
  );
};

export default BlogForm;
