import { Button, Fade, Link, Slide, TextField, Typography } from "@mui/material";
import { useState } from "react";
import userService from "../services/users";
import { useMutation, useQueryClient } from "react-query";
const RegisterForm = ({isRegister,setIsRegister}) => {
  const [nameReg, setNameReg] = useState("");
  const [usernameReg, setUsernameReg] = useState("");
  const [passwordReg, setPasswordReg] = useState("");
  const queryClient = useQueryClient()
  const createUserMutation = useMutation(userService.createUser,{
    onSuccess: () => queryClient.invalidateQueries("users"),
  });
  const createUser = (e) => {
    e.preventDefault();
    const user = {
      name: nameReg,
      username: usernameReg,
      password: passwordReg,
    };
    createUserMutation.mutate(user);
    setIsRegister(false)
  };
  return (
    <Fade direction="left" in={isRegister} mountOnEnter unmountOnExit>
    <div>
      <Typography variant="h5">Create Your Account</Typography>
      <form onSubmit={createUser}>
        <div>
          <TextField
            sx={{ my: 2 }}
            label="Full Name"
            value={nameReg}
            onChange={(e) => setNameReg(e.target.value)}
          />
        </div>

        <div>
          <TextField
            label="Username"
            variant="outlined"
            type="text"
            value={usernameReg}
            id="Username"
            name="Username"
            onChange={(e) => setUsernameReg(e.target.value)}
            sx={{ my: 2 }}
          />
        </div>
        <div>
          <TextField
            sx={{ my: 2 }}
            label="Password"
            type="password"
            value={passwordReg}
            id="Password"
            name="Password"
            onChange={(e) => setPasswordReg(e.target.value)}
          />
        </div>
        <Button type="submit" variant="contained" sx={{ my: 2,background:"#42b72a" }}>
          Register
        </Button>
      </form>
      <Link
        underline="none"
        component={Button}
        onClick={() => {
          setIsRegister(false);
        }}
      >
       <Typography> Already have an account?</Typography>
      </Link>
    </div>
    </Fade>
  );
};
export default RegisterForm;
