import { Box, Button, Fade, TextField, Typography } from "@mui/material";
import PropTypes from "prop-types";

const LoginForm = (props) => {
  return (
    <Fade in = {!props.isRegister}>
    <Box textAlign="center">
      <Typography variant="h5">Log in to the application</Typography>
      <form onSubmit={props.logUser}>
        <div>
          <TextField
            label="Username"
            variant="outlined"
            type="text"
            value={props.username}
            id="Username"
            name="Username"
            onChange={(e) => props.setUsername(e.target.value)}
            sx={{my:2}}
            color="primary"
          />
        </div>
        <div>
          <TextField
            label="Password"
            type="password"
            value={props.password}
            id="Password"
            name="Password"
            onChange={(e) => props.setPassword(e.target.value)}
          />
        </div>
        <Button type="submit" variant="contained" color="primary" sx={{ my: 2 }}>
          login
        </Button>
      </form>
    </Box>
    </Fade>
  );
};
LoginForm.propTypes = {
  username: PropTypes.string.isRequired,
  password: PropTypes.string.isRequired,
};
export default LoginForm;
