import {
  Link,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  Typography,
} from "@mui/material";
import { Link as RouterLink } from "react-router-dom";
const Users = ({ userStatus, userData }) => {
  if (userStatus === "loading") {
    return <div>loading users...</div>;
  }
  return (
    <div>
      <Typography variant="h3">Users</Typography>
      
      <TableContainer component={Paper}>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell>
                <Typography>Users</Typography>
              </TableCell>
              <TableCell>
                <Typography>Motives</Typography>
              </TableCell>
            </TableRow>
            {userData.map((user) => (
              <TableRow key={user.id}>
                <TableCell>
                  <Link
                    component={RouterLink}
                    underline="none"
                    to={`/users/${user.id}`}
                  >
                    <Typography> {user.username}</Typography>
                  </Link>
                </TableCell>
                <TableCell>
                  <Typography>{user.blogs.length}</Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default Users;
