import { useContext, useEffect, useState } from "react";
import NotificationContext from "../NotificationContext";
import blogs from "../services/blogs";
import { useMutation, useQuery, useQueryClient } from "react-query";
import { useNavigate, useParams, Link as RouterLink } from "react-router-dom";
import {
  Box,
  Button,
  Container,
  Grid,
  Link,
  TextField,
  Typography,
} from "@mui/material";
import UserContext from "../UserContext";
import { Person } from "@mui/icons-material";
import goals from "../uzZN04kgalDec8Ng-500x_.jpg";
import users from "../services/users";
const Blog = ({ blogList }) => {
  const { likeNotif } = useContext(NotificationContext);
  const queryClient = useQueryClient();
  const { deleteNotif, commentNotif } = useContext(NotificationContext);
  const [comment, setComment] = useState("");
  const [there, setThere] = useState(false);
  const navigate = useNavigate();
  const { user } = useContext(UserContext);
  const { data: userData, status: userStatus } = useQuery(
    "users",
    users.getAll
  );
  const id = useParams().id;

  const blog = blogList.isLoading
    ? { user: " ", name: " ", members: [] }
    : blogList.data.find((blog) => blog.id === id);

  const removeBlogMutation = useMutation(blogs.remove, {
    onSuccess: () => {
      queryClient.invalidateQueries("blogs");
    },
    onError: () => console.log("uh oh"),
  });
  useEffect(() => {
    const isUserToggled = blog.members.includes(user.id);
    setThere(isUserToggled);
  }, [blog.members]);
  const removeBlog = (blog) => async () => {
    console.log(blog);
    if (window.confirm(`Are you sure you want to remove this motive`)) {
      deleteNotif(blog);
      await removeBlogMutation.mutateAsync(blog.id);
      navigate("/");
    }
  };
  const changeLikeMutation = useMutation(blogs.update, {
    onSuccess: () => queryClient.invalidateQueries("blogs"),
  });
  const likeHandler = async () => {
    likeNotif();
    console.log(blog);
    if (!there)
      changeLikeMutation.mutate({
        ...blog,
        members: blog.members.concat(user.id),
        user: blog.user.id,
      });
    else {
      changeLikeMutation.mutate({
        ...blog,
        members: blog.members.filter((member) => member !== user.id),
        user: blog.user.id,
      });
    }
    setThere(!there);
  };
  const createCommentMutation = useMutation(blogs.createComment, {
    onSuccess: () => queryClient.invalidateQueries("blogs"),
  });
  if (!blogList || !userData) {
    return <div>loading...</div>;
  }
  const createComment = async (e) => {
    e.preventDefault();
    createCommentMutation.mutateAsync({ comment, id: blog.id, user });
    commentNotif();
    setComment("");
  };
  return (
    <Container>
      <Grid container>
        <Grid item xs={12} sx={{ my: 2 }}>
          <Typography variant="h2" align="center">
            {blog.motive}
          </Typography>
        </Grid>
        <Grid item xs={12} container>
          <Grid item xs={6}>
            <img src={goals} alt="motive" />
          </Grid>
          <Grid item container xs={6} textAlign="center">
            
              {blog.members &&
                blog.members.map((member) => (
                  <Grid item xs={6} key={member} sx={{ border: "solid" }} alignItems="center">
                    <Typography>
                      {userData.filter((user) => user.id === member)[0].name}
                    </Typography>
                  </Grid>
                ))}
          
          </Grid>
          <Grid item xs={12}>
            <Box
              display="flex"
              flexDirection="column"
              alignItems="center"
              gap={5}
            >
              <Box display="flex" justifyContent="end" gap={1}>
                <Person />
                <Typography>
                  {blog.members.length}/{blog.pop}{" "}
                </Typography>
              </Box>
              <div>
                <Typography variant="h5">
                  This motive was created by {blog.user.name}
                </Typography>
              </div>{" "}
              <Box display="flex" flexDirection="column" alignItems="center">
                <Typography variant="h6">
                  {blog.members.length}{" "}
                  {blog.members.length === 1
                    ? "person is coming"
                    : "people are coming"}
                </Typography>{" "}
                <Button
                  variant="outlined"
                  onClick={likeHandler}
                  size="large"
                  sx={{ width: 200 }}
                >
                  {there ? "I'm not Coming" : "I'm There"}
                </Button>
              </Box>{" "}
              <Button
                color="secondary"
                variant="contained"
                onClick={removeBlog(blog)}
              >
                Cancel The Motive
              </Button>
            </Box>
          </Grid>
        </Grid>
        <Grid item xs={12} align="center" sx={{ mt: 2 }}>
          <Typography variant="h5">Comments</Typography>
        </Grid>
        <Grid item xs={12}>
          <form onSubmit={createComment}>
            <Box width="100%">
              <TextField
                value={comment}
                onChange={(e) => setComment(e.target.value)}
                multiline
                rows={3}
                fullWidth
                placeholder="Add a comment"
              />
            </Box>
            <Box display="flex" justifyContent="center">
              <Button type="submit" variant="contained" sx={{ my: 3 }}>
                add comment
              </Button>
            </Box>
          </form>
        </Grid>
        <ul style={{ listStyleType: "none" }}>
          {blog.comments.map((comment) => (
            <li
              key={Math.floor(Math.random() * 10000)}
              style={{ marginBottom: 20 }}
            >
              <Link
                underline="none"
                component={RouterLink}
                to={`/users/${comment.user.id}`}
              >
                @{comment.user.name}
              </Link>
              <Typography>{comment.comment}</Typography>
            </li>
          ))}
        </ul>
      </Grid>
    </Container>
  );
};

export default Blog;
