import { Box, Button, Grid, Link, Typography } from "@mui/material";
import { useContext, useState } from "react";
import LoginForm from "./LoginForm";
import RegisterForm from "./RegisterForm";
import newLogo from "../newLogo.png";
import login from "../services/login";
import UserContext from "../UserContext";
import blogs from "../services/blogs";
import { useNavigate } from "react-router-dom";
const LoginPage = () => {
  const [isRegister, setIsRegister] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const { user, userDispatch } = useContext(UserContext);
  const navigate = useNavigate();
  const logUser = async (e) => {
    e.preventDefault();
    try {
      const user = await login({
        username: username,
        password: password,
      });
      userDispatch({ type: "username", payload: username });
      userDispatch({ type: "password", payload: password });
      userDispatch({ type: "name", payload: user.name });
      userDispatch({ type: "id", payload: user.id });
      console.log(user);
      setUsername("");
      setPassword("");
      blogs.setToken(user.token);
      window.localStorage.setItem("storedUser", JSON.stringify(user));
      navigate("/");
    } catch (exception) {
      console.error(exception);
    }
  };
  return (
    <Grid container align="center" height="100vh">
      <Grid item xs={12} height="25%" sx={{ mt: 8 }}>
        <img src={newLogo} alt="logo" height="100%"></img>
      </Grid>
      <Grid item xs={6}>
        <Typography variant="h2" textAlign="center" color="#4b3935">
          Welcome
        </Typography>
        <Typography variant="h4" textAlign="center" sx={{ my: 6 }}>
          This app is just for testing
        </Typography>
      </Grid>
      <Grid item xs={6}>
        <Box textAlign="center">
          {isRegister ? (
            <RegisterForm
              setIsRegister={setIsRegister}
              isRegister={isRegister}
            />
          ) : (
            <LoginForm
              username={username}
              password={password}
              setUsername={setUsername}
              setPassword={setPassword}
              logUser={logUser}
              isRegister={isRegister}
            />
          )}
          {!isRegister && (
            <Link
              underline="none"
              component={Button}
              onClick={() => {
                setIsRegister(true);
              }}
            >
              <Typography> New user? Create new account</Typography>
            </Link>
          )}
        </Box>
      </Grid>
    </Grid>
  );
};

export default LoginPage;
