import { Link as RouterLink } from "react-router-dom";
import Togglable from "./Toggleable";
import BlogForm from "./BlogForm";
import { Box, Paper, Typography, Link } from "@mui/material";
import goals from "../uzZN04kgalDec8Ng-500x_.jpg";

const BlogList = ({
  blogList,
  createBlog,
  setNewBlog,
  newBlog,
  blogFormRef,
}) => {
  return (
    <div>
      <Typography variant="h3" sx={{ mt: 2 }}>
        Active Motives
      </Typography>
      <Togglable buttonLabel="Create Motive" ref={blogFormRef}>
        <BlogForm
          createBlog={createBlog}
          newBlog={newBlog}
          setNewBlog={setNewBlog}
        />
      </Togglable>
      {blogList &&
        blogList
          .sort((a, b) => b.likes - a.likes)
          .map((blog) => (
            <Paper key={blog.id} elevation={3}>
              <Box sx={{ m: 3 }}>
                <Link
                  to={`/blogs/${blog.id}`}
                  underline="none"
                  component={RouterLink}
                  display="flex"
                >
                  <Typography variant="h3"> {blog.motive}</Typography>
                  <img src={goals}/>
                </Link>
              </Box>
            </Paper>
          ))}{" "}
    </div>
  );
};

export default BlogList;
