import { Box, Container, Typography } from "@mui/material"
import { useParams } from "react-router-dom"
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Grid from '@mui/material/Grid';
const User = ({userStatus,userData}) =>{
    const id = useParams().id
    if(userStatus === "loading"){
        return <div>loading..</div>
    }
    const user = userData.find(n => n.id === id)
    const motives = [1, 2, 3, 4, 5, 6, 7, 8, 9];

    return(<Box>
        <Typography component="h1" variant="h2" align="center" mb={4}>{user.username}</Typography>
        <Typography variant="h3" align="center">Motives</Typography>
        <ul>
        {user.blogs.map(blog=><li key ={blog.id}>{blog.category}</li>)}
        </ul>
        <Container sx={{ py: 8 }} maxWidth="md">
          <Grid container spacing={4}>
            {motives.map((card) => (
              <Grid item key={card} xs={12} sm={6} md={4}>
                <Card
                  sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
                >
                  <CardMedia
                    component="div"
                    sx={{
                      // 16:9
                      pt: '56.25%',
                    }}
                    image="https://source.unsplash.com/random?wallpapers"
                  />
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography gutterBottom variant="h5" component="h2">
                      Heading
                    </Typography>
                    <Typography>
                      This is a media card. You can use this section to describe the
                      content.
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button size="small">View</Button>
                    <Button size="small">Edit</Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
    </Box>) 

}

export default User