import { AppBar, Box, Button, Link, Toolbar, Typography } from "@mui/material";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { Link as RouterLink } from "react-router-dom";
import UserContext from "../UserContext";
const Navbar = () => {
  const { user, userDispatch } = useContext(UserContext);
  const navigate = useNavigate();

  return (
    <AppBar position="sticky" color="background">
      <Toolbar position="static">
        <Link component={RouterLink} to="/" sx={{ mr: 2 }} underline="none">
          <Typography variant="h4" color="#4b3935">
            Active
          </Typography>
        </Link>
        <Link component={RouterLink} to="/" sx={{ mr: 2 }} underline="none">
          <Typography variant="h5">Motives</Typography>
        </Link>
        <Link
          component={RouterLink}
          to="/users"
          sx={{ mr: 2 }}
          underline="none"
        >
          <Typography variant="h5">Users</Typography>
        </Link>
        <Box sx={{ ml: "auto" }} display="flex" alignItems="center" gap={4}>
          <Typography>Welcome {user.name}</Typography>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              window.localStorage.removeItem("storedUser");
              userDispatch({ type: "null" });
              navigate("/");
            }}
          >
            Logout
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
